import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http'; 

import { AppComponent } from './app.component';
import { ChartjsModule } from '@ctrl/ngx-chartjs';
import { NgxChartsModule } from '@swimlane/ngx-charts'
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { MatToolbarModule, MatIconModule, MatButtonModule, MatCardModule } from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HeaderComponent } from './feature/header/header.component';
import { OrganizationComponent } from './feature/organization/organization.component';
import { OrgInsightsComponent } from './feature/org-insights/org-insights.component';
import { OrganizationService } from './core/services/organization.services';
import { OrgInsightsDatatableComponent } from './feature/org-insights/org-insights-datatable/org-insights-datatable.component';
import { OrganizationDatatableComponent } from './feature/organization/organization-datatable/organization-datatable.component'

const appRoutes: Routes = [
  { path: '', component: OrgInsightsComponent, pathMatch: 'full'},
  { path: 'organization/:id', component: OrganizationComponent },
  { path: '**', component: OrgInsightsComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    OrganizationComponent,
    OrgInsightsComponent,
    OrgInsightsDatatableComponent,
    OrganizationDatatableComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    ChartjsModule,
    NgxChartsModule,
    NgxDatatableModule,
    MatToolbarModule,
    MatIconModule,
    MatCardModule,
    BrowserAnimationsModule,
    HttpClientModule,
  ],
  providers: [OrganizationService],
  bootstrap: [AppComponent]
})
export class AppModule { }
