import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'; 
import { Observable } from 'rxjs';

@Injectable()

export class OrganizationService {

    constructor(private http: HttpClient) {
    }

    getOrgnizations(): Observable<any> {
        return this.http.get("assets/Org.json");
    }

    getOrgTypes(): Observable<any> {
        return this.http.get("assets/OrgType.json");
    }

    getRiskStatOrgToOrgs(): Observable<any> {
        return this.http.get("assets/riskStatOrgToOrg.json");
    }

    getRiskStatOrg(): Observable<any> {
        return this.http.get("assets/riskStatOrg.json");
    }

}