module.exports = ".example-icon {\n  padding: 0 14px;\n}\n\n.example-spacer {\n  flex: 1 1 auto;\n}\n\n.filter-ul {\n    list-style-type: none;\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n    background-color: white;\n    top: 0;\n    width: 100%;\n    border-bottom: 1px solid gray;\n}\n\n.filter-li {\n    float: left;\n}\n\n.filter-li a {\n    display: block;\n    color: rgba(0, 0, 0, 0.50);\n    text-align: center;\n    padding: 10px 23px;\n    text-decoration: none;\n    font-size: 12px;\n    font-weight: 700;\n}\n\n.filter-li a:hover:not(.active) {\n    background-color: #E3DEDE;\n    border-bottom: 2px #E3DEDE solid;\n    cursor: pointer\n}\n\n.active {\n    border-bottom: 2px #5B8ED0 solid;\n    cursor: pointer;\n    color: #5B8ED0 !important;\n}\n\n.legend-list{\n    list-style-type: none; \n    padding:5px 30px 5px 5px;\n}\n\n.legend-list li{\n    color:#fff; \n    margin-top:10px; \n    padding: 5px 10px; \n    border-radius: 25px;\n    font-size: 12px;\n    cursor: pointer;\n}\n\n.type-selector{\n    width: 40px; \n    border: none; \n    background-color: #fff;\n    color: #5B8ED0;\n    font-weight: 600;\n    line-height: 12px;\n    cursor: pointer;\n}\n\n.type-selector-icon{\n    font-size:16px;\n    font-weight:600;\n}\n\n.total-label{\n    float:right; \n    font-size:12px; \n    padding-top: 12px; \n    color: gray\n}\n\n#pie-chart-parent { display: -ms-grid; display: grid; }\n\n.graph-title{\n    color: rgba(0, 0, 0, 0.6); \n    font-weight: 500; \n    font-size: 24px;\n}\n\n.graph-date{\n    color: rgba(0, 0, 0, 0.6); \n    font-weight: 500; \n    font-size: 18px;\n}\n\n.filter-out{\n    background-color: rgba(0, 0, 0, 0.4) !important;\n}\n\n.disabled{\n    color: rgba(0, 0, 0, 0.4) !important;\n}"